import { getAPIConfig } from "@/lib/ai-utils"
import { createClient } from '@supabase/supabase-js'

export const maxDuration = 60 // ‰ΩúÊñáÊâπÊîπÊØîËæÉÊÖ¢ÔºåÈÄÇÂΩìÂª∂ÈïøË∂ÖÊó∂Êó∂Èó¥

// üí∞ ÂÆö‰ª∑Á≠ñÁï• (Ê†πÊçÆ‰Ω†ÁöÑÂõæÁâáÊé®ÂØº)
const COST_ESSAY = 250 // ‰ΩúÊñáÊâπÊîπÔºö250 ÁßØÂàÜ/Ê¨°
const COST_CHAT = 20   // ÊôÆÈÄöÂØπËØùÔºö20 ÁßØÂàÜ/Ê¨°

export async function OPTIONS(req: Request) {
  return new Response(null, {
    status: 200,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'POST, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization, X-AI-Provider, X-AI-Model',
    },
  })
}

export async function POST(req: Request) {
  try {
    console.log("[v0] Chat API called")

    const body = await req.json()
    const { messages, files, extractedText, userId }: { messages: any[]; files?: any[]; extractedText?: string, userId?: string } = body

    // Âà§Êñ≠ÊòØÂê¶‰∏∫‰ΩúÊñáËØ∑Ê±Ç (ÊúâÊèêÂèñÁöÑÊñáÊú¨‰∏îÈïøÂ∫¶Â§ß‰∫é 100)
    const isEssayRequest = extractedText && extractedText.length > 100
    
    // Á°ÆÂÆöÊú¨Ê¨°Êìç‰ΩúÁöÑË¥πÁî®
    const currentCost = isEssayRequest ? COST_ESSAY : COST_CHAT

    // ==========================================
    // üõë Á¨¨‰∏ÄÂÖ≥ÔºöÂÆâÊ£Ä (Ê£ÄÊü•ÁßØÂàÜ)
    // ==========================================
    if (!userId) {
       console.warn("‚ö†Ô∏è Ë≠¶Âëä: ÂâçÁ´ØÊú™‰º†ÈÄí userIdÔºåÊó†Ê≥ïÊâ£Ë¥πÔºÅ(ÊöÇÊó∂ÊîæË°åÔºå‰ΩÜÂú®Áîü‰∫ßÁéØÂ¢ÉÂ∫îÊã¶Êà™)")
       // return new Response(JSON.stringify({ error: "Êú™ÁôªÂΩïÔºåÊó†Ê≥ï‰ΩøÁî®" }), { status: 401 }) 
    } 
    else {
      const supabaseAdmin = createClient(
        process.env.NEXT_PUBLIC_SUPABASE_URL!,
        process.env.SUPABASE_SERVICE_ROLE_KEY!
      )

      // Êü•‰ΩôÈ¢ù
      const { data: creditData, error: creditError } = await supabaseAdmin
        .from('user_credits')
        .select('credits')
        .eq('user_id', userId)
        .single()
      
      if (creditError || !creditData) {
         console.error("Êü•ËØ¢ÁßØÂàÜÂ§±Ë¥•:", creditError)
      } else {
        console.log(`[Billing] Áî®Êà∑ ${userId} ÂΩìÂâçÁßØÂàÜ: ${creditData.credits}, Êú¨Ê¨°ÈúÄÊâ£Èô§: ${currentCost}`)
        
        // Ê†∏ÂøÉÂà§Êñ≠Ôºö‰ΩôÈ¢ùÊòØÂê¶Ë∂≥Â§üÊîØ‰ªòÊú¨Ê¨°Ë¥πÁî®
        if (creditData.credits < currentCost) {
          return new Response(JSON.stringify({ 
            error: `ÁßØÂàÜ‰∏çË∂≥ÔºÅÊú¨Ê¨°Êìç‰ΩúÈúÄË¶Å ${currentCost} ÁßØÂàÜÔºåÊÇ®ÂΩìÂâç‰ªÖÂâ© ${creditData.credits} ÁßØÂàÜ„ÄÇËØ∑ÂâçÂæÄÂçáÁ∫ßÂ•óÈ§ê„ÄÇ` 
          }), {
            status: 402, // Payment Required
            headers: { "Content-Type": "application/json" }
          })
        }
      }
    }
    // ==========================================

    // ÂÆö‰πâÊâ£Ë¥πÂáΩÊï∞ (ÂêéÁª≠Ë∞ÉÁî®)
    const deductCredit = async () => {
      if (!userId) return
      try {
        const supabaseAdmin = createClient(
          process.env.NEXT_PUBLIC_SUPABASE_URL!,
          process.env.SUPABASE_SERVICE_ROLE_KEY!
        )
        const { data } = await supabaseAdmin.from('user_credits').select('credits').eq('user_id', userId).single()
        
        if (data) {
          // Á°Æ‰øù‰∏çÊâ£ÊàêË¥üÊï∞
          const newCredits = Math.max(0, data.credits - currentCost)
          await supabaseAdmin
            .from('user_credits')
            .update({ credits: newCredits })
            .eq('user_id', userId)
          console.log(`[Billing] Êâ£Ë¥πÊàêÂäü (-${currentCost})ÔºåÂâ©‰Ωô: ${newCredits}`)
        }
      } catch (e) {
        console.error("[Billing] Êâ£Ë¥πÂ§±Ë¥•:", e)
      }
    }

    // --- ÂàÜÊîØ A: ‰ΩúÊñáÊâπÊîπÊ®°Âºè ---
    if (isEssayRequest) {
      console.log("[v0] Triggering grading workflow...")

      try {
        // Ë∞ÉÁî®‰∏ìÈó®ÁöÑÊâπÊîπÊé•Âè£ (ÂÅáËÆæ‰Ω†Â∑≤ÁªèÊúâ‰∫ÜËøô‰∏™Êé•Âè£)
        const essayGradeResponse = await fetch(`${req.url.replace("/api/chat", "/api/essay-grade")}`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            essayText: extractedText,
            gradeLevel: "Âàù‰∏≠", // ËøôÈáåÂèØ‰ª•Ê†πÊçÆÁî®Êà∑ËæìÂÖ•Âä®ÊÄÅË∞ÉÊï¥
            topic: "‰ΩúÊñáÊâπÊîπ",
            wordLimit: "800Â≠ó",
          }),
        })

        if (!essayGradeResponse.ok) throw new Error("Essay grading failed")

        const essayResult = await essayGradeResponse.json()

        // ‚úÖ Âè™ÊúâÊàêÂäüÊãøÂà∞ÁªìÊûúÊâçÊâ£Ë¥π (ËâØÂøÉÂïÜÂÆ∂)
        await deductCredit()

        const encoder = new TextEncoder()
        const stream = new ReadableStream({
          start(controller) {
            const resultJSON = JSON.stringify(essayResult.result, null, 2)
            const fullText = "\`\`\`json\n" + resultJSON + "\n\`\`\`"
            const aiSDKChunk = `0:${JSON.stringify({ type: "text-delta", textDelta: fullText })}\n`
            controller.enqueue(encoder.encode(aiSDKChunk))
            const finishChunk = `0:${JSON.stringify({ type: "finish", finishReason: "stop" })}\n`
            controller.enqueue(encoder.encode(finishChunk))
            controller.close()
          },
        })

        return new Response(stream, {
          headers: {
            "Content-Type": "text/plain; charset=utf-8",
            "X-Vercel-AI-Data-Stream": "v1",
            "Cache-Control": "no-cache",
            Connection: "keep-alive",
            'Access-Control-Allow-Origin': '*',
          },
        })
      } catch (error) {
        console.error("[v0] Essay grading error:", error)
        throw new Error("Essay grading failed")
      }
    }

    // --- ÂàÜÊîØ B: Dify ÂØπËØùÊ®°Âºè ---
    const provider = req.headers.get("X-AI-Provider") || "openai"
    const modelName = req.headers.get("X-AI-Model") || "gpt-4o"
    const customConfig = getAPIConfig(provider)

    if (!customConfig) {
      return new Response(JSON.stringify({ error: "API configuration not found" }), {
        status: 500,
        headers: { "Content-Type": "application/json" }
      })
    }

    // ÈáçÊñ∞ÊûÑÂª∫Ê∂àÊÅØ
    const apiMessages = messages.map((msg, index) => {
      let textContent = ""
      if (msg.parts && Array.isArray(msg.parts)) {
        textContent = msg.parts.filter((part: any) => part.type === "text").map((part: any) => part.text).join("\n")
      } else if (msg.content) {
         // Â§ÑÁêÜ Vercel AI SDK ‰∏çÂêåÁâàÊú¨ÁöÑ content Ê†ºÂºè
        if (Array.isArray(msg.content)) {
          textContent = msg.content.filter((part: any) => part.type === "text").map((part: any) => part.text).join("\n")
        } else if (typeof msg.content === "string") {
          textContent = msg.content
        } else {
          textContent = String(msg.content || "")
        }
      }

      // Êñá‰ª∂Â§ÑÁêÜÈÄªËæë (‰øùÊåÅÂéüÊ†∑)
      if (index === messages.length - 1 && msg.role === "user" && files && files.length > 0) {
         const hasImages = files.some((file) => file.type.startsWith("image/"))
         if (hasImages) {
             const content: any[] = []
             if (textContent.trim()) content.push({ type: "text", text: textContent })
             for (const file of files) {
                if (file.type.startsWith("image/")) {
                  content.push({ type: "image_url", image_url: { url: file.data } })
                }
             }
             return { role: msg.role, content: content }
         } else {
             let fileContext = ""
             for (const file of files) {
                if (file.type === "text/plain") {
                    try {
                        const base64Data = file.data.split(",")[1]
                        const fileTextContent = Buffer.from(base64Data, "base64").toString("utf-8")
                        fileContext += `\n\n[Êñá‰ª∂: ${file.name}]\n${fileTextContent}`
                    } catch(e) {}
                }
             }
             return { role: msg.role, content: textContent + fileContext }
         }
      }
      return { role: msg.role, content: textContent }
    })

    const response = await fetch(`${customConfig.baseURL}/chat/completions`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${customConfig.apiKey}`,
      },
      body: JSON.stringify({
        model: modelName,
        messages: apiMessages,
        stream: true,
        temperature: 0.7,
        max_tokens: 4000,
      }),
    })

    if (!response.ok) {
      const errorText = await response.text()
      console.error("[v0] API error:", response.status, errorText)
      return new Response(JSON.stringify({ error: `AI ÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®: ${response.status}` }), {
        status: response.status,
        headers: { "Content-Type": "application/json" }
      })
    }

    // ‚úÖ ÊôÆÈÄöÂØπËØùÂºÄÂßãÊµÅÂºè‰º†ËæìÂâçÔºåÊâßË°åÊâ£Ë¥π
    await deductCredit()

    const encoder = new TextEncoder()
    const decoder = new TextDecoder()

    const stream = new ReadableStream({
      async start(controller) {
        const reader = response.body?.getReader()
        if (!reader) {
          controller.close()
          return
        }
        try {
          let buffer = ""
          while (true) {
            const { done, value } = await reader.read()
            if (done) break
            const decoded = decoder.decode(value, { stream: true })
            buffer += decoded
            const lines = buffer.split("\n")
            buffer = lines.pop() || ""
            for (const line of lines) {
              const trimmed = line.trim()
              if (!trimmed || trimmed === "data: [DONE]") continue
              if (trimmed.startsWith("data: ")) {
                try {
                   const jsonStr = trimmed.slice(6)
                   const data = JSON.parse(jsonStr)
                   if (data.choices && data.choices[0]?.delta?.content) {
                     const content = data.choices[0].delta.content
                     const aiSDKChunk = `0:${JSON.stringify({ type: "text-delta", textDelta: content })}\n`
                     controller.enqueue(encoder.encode(aiSDKChunk))
                   }
                } catch (e) {}
              }
            }
          }
          controller.close()
        } catch (error) {
          controller.error(error)
        }
      },
    })

    return new Response(stream, {
      headers: {
        "Content-Type": "text/plain; charset=utf-8",
        "X-Vercel-AI-Data-Stream": "v1",
        "Cache-Control": "no-cache",
        Connection: "keep-alive",
        'Access-Control-Allow-Origin': '*',
      },
    })
  } catch (error) {
    console.error("[v0] Chat API error:", error)
    return new Response(JSON.stringify({ error: "Internal server error" }), {
      status: 500,
      headers: { "Content-Type": "application/json" }
    })
  }
}